<template>
  <flex-container class="cron-tab-page" direction='column' wrap='nowrap'>
    <flex-item>
      <el-alert class="margin-bottom-10" title="Json" type="success" :closable='false' description="CodeMirrorJson">
      </el-alert>
    </flex-item>
    <flex-container class="margin-bottom-10">
      <flex-item width='100%'>
        <json-editor ref="jsonEditor" v-model="value" />
      </flex-item>
    </flex-container>
  </flex-container>
</template>

<script>
import JsonEditor from "@/components/JsonEditor/index";
const jsonData =
  '[{"items":[{"market_type":"forexdata","symbol":"XAUUSD"},{"market_type":"forexdata","symbol":"UKOIL"},{"market_type":"forexdata","symbol":"CORN"}],"name":""},{"items":[{"market_type":"forexdata","symbol":"XAUUSD"},{"market_type":"forexdata","symbol":"XAGUSD"},{"market_type":"forexdata","symbol":"AUTD"},{"market_type":"forexdata","symbol":"AGTD"}],"name":""},{"items":[{"market_type":"forexdata","symbol":"CORN"},{"market_type":"forexdata","symbol":"WHEAT"},{"market_type":"forexdata","symbol":"SOYBEAN"},{"market_type":"forexdata","symbol":"SUGAR"}],"name":""},{"items":[{"market_type":"forexdata","symbol":"UKOIL"},{"market_type":"forexdata","symbol":"USOIL"},{"market_type":"forexdata","symbol":"NGAS"}],"name":""}]';

export default {
  components: { JsonEditor },
  data() {
    return {
      value: JSON.parse(jsonData)
    };
  }
};
</script>



